import { FC } from 'react'
import { useHistory } from 'react-router-dom'
import SwitchMode from './SwitchMode'
import { $LOGIN, $USER } from 'routes'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faShoppingCart, faHeart, faSignOutAlt, faUser } from '@fortawesome/free-solid-svg-icons'

const IconsNav: FC = (): JSX.Element => {
  const history = useHistory()
  const { pathname } = history.location

  return <div className="iconsHeader">
    <SwitchMode />
    <FontAwesomeIcon icon={faShoppingCart} className="IconsNav" title="Shopping cart" />
    {
      pathname === $USER
        ? <>
          <FontAwesomeIcon icon={faHeart} className="IconsNav" title="Favorites" />
          <FontAwesomeIcon icon={faSignOutAlt} className="IconsNav" title="Logout" />
        </>
        : <FontAwesomeIcon icon={faUser} className="IconsNav" title="Login" onClick={() => history.push($LOGIN)} />
    }

  </div>
}

export default IconsNav