import { FC } from 'react'
import useTheme from 'hooks/useTheme'
import { LocalStorage } from 'services/LocalStorage.service'
import 'styles/SwitchMode.css'

const SwitchMode: FC = (): JSX.Element => {
  const { mode, setMode } = useTheme()

  const handleMode = () => setMode && setMode(mode => mode === 'dark' ? 'light' : 'dark')

  LocalStorage.set('ModeUser', mode)

  return <div className="toggle">
    <input type="checkbox" className="checkbox" id="checkbox" onChange={handleMode}/>
    <label htmlFor="checkbox" className="labelToggle" title="Change Theme" >
      <div className="ballLabelToggle"></div>
    </label>
  </div>
}

export default SwitchMode