import { useState } from 'react'
import { DivSearchBox } from 'components/Header/style'
import { ChangeEventForm, ChangeEventInput } from 'types'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch, faTimes } from '@fortawesome/free-solid-svg-icons'
import 'styles/FormSearch.css'

const FormSearch = (): JSX.Element => {
  const [keyword, setKeyword] = useState<string>('')
  const [widthSearch, setWidthSearch] = useState<number>(0)

  const handleSubmit: ChangeEventForm = (e) => {
    e.preventDefault()
    console.log('enviar')
  }

  const handleChange: ChangeEventInput = (e) => setKeyword(e.target.value)

  const restartKeyword = () => keyword.length > 0 && setKeyword('')

  const handleWidth = () => {
    widthSearch === 0 ? setWidthSearch(26) : setWidthSearch(0)
    restartKeyword()
  }

  return <form className="searchForm" onSubmit={handleSubmit}>
    <DivSearchBox width={widthSearch} >
      <input type="text" className="inputSearch" value={keyword} onChange={handleChange} placeholder="Buscar" aria-label="input search"/>
      {
        keyword.length > 0 && <>
          <FontAwesomeIcon icon={faTimes} onClick={restartKeyword} />
          <button className="btnSearch" aria-label="Search">Search</button>
        </>
      }
    </DivSearchBox>
    <label htmlFor="checkSearch" className="labelSearch" onClick={handleWidth}>
      <FontAwesomeIcon icon={faSearch} />
    </label>
  </form>
}

export default FormSearch