import { lazy } from 'react'
import { Link } from 'react-router-dom'
import { LiNav } from 'components/Nav/style'
import { menuList } from 'utils/menuList'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleDown, faAngleRight } from '@fortawesome/free-solid-svg-icons'
import { $HOME } from 'routes'

interface IListNav {
  width?: number
}

const SubListNav = lazy(() => import('./SubListNav'))

const ListNav = ({ width }: IListNav): JSX.Element => {
  const normalNav: boolean = width ? width >= 14 : false

  const responsiveWidth: boolean = window.innerWidth > 600

  const setPath = (path: string) => {
    const pathLower = path.toLowerCase().replace(/\s+/g, '')
    return pathLower === 'home' ? $HOME : $HOME + '/' + pathLower
  }

  return <ul>
    {
      menuList.map(menu => {
        const { name, icon, subMenu } = menu
        return <LiNav key={name}>
          <Link to={setPath(name)} >
            <FontAwesomeIcon icon={icon} />
            { normalNav && <span>{name}</span> }
          </Link>
          {
            subMenu !== undefined && <>
              <FontAwesomeIcon icon={responsiveWidth ? faAngleRight : faAngleDown} />
              <SubListNav subMenu={subMenu} />
            </>
          }
        </LiNav>
      })
    }
  </ul>
}

export default ListNav