import { FC, useState } from 'react'
import { NavBar } from 'components/Nav/style'
import ListNav from './ListNav'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleDoubleDown, faAngleDoubleLeft, faAngleDoubleRight, faAngleDoubleUp } from '@fortawesome/free-solid-svg-icons'
import { IconDefinition } from '@fortawesome/fontawesome-common-types'

const inicialState: number = window.innerWidth > 600 ? 14 : 4

const Nav: FC = (): JSX.Element => {
  const [widthNav, setWidthNav] = useState<number>(inicialState)

  const sizeNavBar = () => widthNav === 14 ? setWidthNav(4) : setWidthNav(14)

  const iconNormal: IconDefinition = widthNav === 14 ? faAngleDoubleLeft : faAngleDoubleRight
  const iconResponsive: IconDefinition = widthNav === 14 ? faAngleDoubleUp : faAngleDoubleDown
  const arrowIconToUse: IconDefinition = window.innerWidth > 600 ? iconNormal : iconResponsive

  return <NavBar width={widthNav}>
    <FontAwesomeIcon icon={arrowIconToUse} onClick={sizeNavBar} />
    <ListNav width={widthNav}/>
  </NavBar>
}

export default Nav