import styled from 'styled-components'

interface INavBar {
  width: number
}

export const NavBar = styled.nav<INavBar>`
  grid-area: nav;
  background: var(--secundary);
  width: ${({ width }) => width}rem;
  height: 100%;
  //min-height: calc(100vh - 5rem);
  position: relative;
  padding: 1.4rem 0 0 0;

  border-radius: .11rem;
  box-shadow:
  .012rem 0 .01rem var(--shadow),
  inset 0 .1rem .06rem var(--secundary);

  & > svg:first-child {
    margin: .4rem;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
  }

  @media ( max-width: 600px ) {
    width: 100%;
    height: 100%;
    padding: 0 1.4rem 0 0;

    border-radius: 0;
    box-shadow:
      -.06rem .02rem .01rem var(--shadow),
      inset .1rem 0.1rem .02rem var(--secundary);

    & > svg:first-child {
      top: auto;
      bottom: 0;
    }

    & ul {
      display: flex;
      flex-direction: ${({ width }) => width >= 14 && 'column'};
      justify-content: space-evenly;
    }
  }
`

export const LiNav = styled.li`
  height: 2.4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: .5rem;
  padding: 0 .8rem;
  border-radius: .2rem;
  position: relative;
  cursor: pointer;
  transition: all .2s ease-in;

  & a {
    width: 100%;
    display: flex;
    align-items: center;
  }

  & span {
    width: 100%;
  }

  &:hover {
    background: var(--contrast);
    box-shadow: inset 0 0 .1rem #000;
  }

  & ul {
    background: var(--primary);
    opacity: 0;
    visibility: hidden;
    position: absolute;
    top: 0;
    left: 80%;
    border-radius: .2rem;
    box-shadow: inset -.02rem -.04rem .1rem var(--contrast);
    transition: all .2s ease-in;
  }

  &:hover > ul {
    opacity: 1;
    left: 100%;
    z-index: 100;
    visibility: visible;
  }

  @media ( max-width: 600px ) {
    & ul {
      top: 100%;
      left: auto;
      right: 0;
      display: flex;
      flex-direction: column;
    }

    &:hover > ul {
      left: auto;
      top: 100%;
    }
  }
`